function CartTile({
  handleNavigation,
  product,
  onIncreaseQuantity,
  onDecreaseQuantity,
  onRemoveProduct,
  quantity,
}) {
  const [tileLoading, setTileLoading] = useState(false);

  const handleOperation = async (operation, productId) => {
    setTileLoading(true);
    try {
      await operation(productId);
    } catch (err) {
      toast.error('Operation failed. Please try again.');
    } finally {
      setTileLoading(false);
    }
  };

  return (
    <div
      className={`bg-white border border-gray-200 rounded-lg shadow-sm overflow-hidden ${
        tileLoading ? 'animate-pulse' : ''
      }`}
    >
      <div className="flex flex-col md:flex-row p-4 gap-4">
        {/* Product Image */}
        <img
          className="w-full md:w-24 h-24 rounded-lg object-cover"
          src={product?.image}
          alt={product?.name}
        />

        {/* Product Details */}
        <div className="flex-1 flex flex-col justify-between">
          <div>
            <h3
              onClick={() => handleNavigation(product?.id)}
              className="text-lg font-semibold text-gray-900 hover:underline cursor-pointer"
            >
              {product?.name}
            </h3>
            <p className="text-sm text-gray-600 mt-1">
              ${product?.offerPrice?.toFixed(2)}{' '}
              <span className="line-through text-gray-400">
                ${product?.actualPrice?.toFixed(2)}
              </span>
            </p>
          </div>

          {/* Quantity Controls */}
          <div className="flex items-center gap-2 mt-3">
            <Button
              variant="outline"
              size="sm"
              onClick={() => handleOperation(onDecreaseQuantity, product?.id)}
              disabled={tileLoading}
            >
              -
            </Button>
            <Input
              className="w-12 text-center"
              type="text"
              value={quantity}
              readOnly
            />
            <Button
              variant="outline"
              size="sm"
              onClick={() => handleOperation(onIncreaseQuantity, product?.id)}
              disabled={tileLoading}
            >
              +
            </Button>
          </div>
        </div>
      </div>

      {/* Remove Button (Fixed at Bottom for Mobile) */}
      <div className="md:hidden fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 p-2 shadow-lg">
        <Button
          variant="destructive"
          size="sm"
          onClick={() => handleOperation(onRemoveProduct, product?.id)}
          disabled={tileLoading}
          className="w-full"
        >
          Remove
        </Button>
      </div>

      {/* Remove Button (Desktop) */}
      <div className="hidden md:block self-center">
        <Button
          variant="destructive"
          size="sm"
          onClick={() => handleOperation(onRemoveProduct, product?.id)}
          disabled={tileLoading}
        >
          Remove
        </Button>
      </div>
    </div>
  );
}